import React, {useState, Fragment} from 'react'
import './CreateProfile.css'
import Autocomplete from 'react-google-autocomplete';
import {connect} from 'react-redux';
import {withRouter,Link} from 'react-router-dom';
import PropTypes from 'prop-types'
import {createProfile} from '../../../actions/profile'
import Alert from '../../alert/Alert'

const CreateProfile = ({createProfile,history,uploadProfilePicture}) => {

    const [formData,setFormData] = useState({
      company:'',
      gender:'',
      website:'',
      imageProfile:'',
      status:'',
      skills:'',
      githubusername:'',
      bio:'',
      twitter:'',
      facebook:'',
      linkedin:'',
      youtube:'',
      instagram:''  
    })

    const {
      company,
      gender,
      website,
      imageProfile,
      status,
      skills,
      githubusername,
      bio,
      twitter,
      facebook,
      linkedin,
      youtube,
      instagram 
    } = formData

    const [displaySocialLinks,toggleBetweenSocial] = useState(false)

    const onChange = e => { 

        console.log(e.target.value)
        setFormData({...formData, [e.target.name]: e.target.value})}
    
    const setImage = e =>{
        setFormData({...formData, imageProfile: e.target.files[0]})
    }
    const onSubmit = e => {
        e.preventDefault()
        //update the profile Picture
        const formImage = new FormData();
        if(imageProfile)
            formImage.append('imageProfile',imageProfile);
        createProfile(formData,history,formImage)
    }
    return (
    <section className="container">
            <h1 className="large text-primary">Create Your Profile</h1>
            <p className="lead">
                <i className="fas fa-user"></i> Let's get some information to make your profile stand out
            </p>
            <p><strong>*</strong> = required field</p>
            <div className="show-alert"><Alert/></div>
            <form className="form-profile">
               <div className="form-group">
                  <select name="status" value={status} onChange={(e) => onChange(e)}>
                    <option value="0">* Select Professional Status</option>
                    <option value="Developer">Developer</option>
                    <option value="Junior Developer">Junior Developer</option>
                    <option value="Senior Developer">Senior Developer</option>
                    <option value="Manager">Manager</option>
                    <option value="Student or Learning">Student or Learning</option>
                    <option value="Instructor">Instructor or Teacher</option>
                    <option value="Intern">Intern</option>
                    <option value="Other">Other</option>
                  </select>
                  <small className="form-text">Give us an idea of where you are at in your career</small>
                </div>

                <div className="form-group">
                     <select name="gender" value={gender} onChange={(e) => onChange(e)}>
                       <option value="0">* Select Your Gender</option>
                       <option value="Male">Male</option>
                       <option value="Female">Female</option>
                     </select>
                   <small className="form-text">Select your Gender</small>
               </div>

               <div className="form-group">
                   <input type="text" placeholder="Company" name="company" value={company}  onChange={(e) => onChange(e)}/>
                   <small className="form-text">Could be your own company or one you are work for</small>
               </div>

               <div className="form-group">
                   <input type="file" placeholder="Upload" name="imageProfile"  onChange={(e) => setImage(e)}/>
                   <small className="form-text">The image must not be bigger then 5MB and only JPG\JPEG\PNG types</small>
               </div>

               <div className="form-group">
                   <input type="text" placeholder="Website" name="website" value={website} onChange={(e) => onChange(e)}/>
                   <small className="form-text">Could be your own or a company website</small>
               </div>

               <div className="form-group">
               <Autocomplete style={{width: '40%',height:'39px' ,fontSize:'18px', border:'1px solid rgb(195, 195, 195)'}}  placeholder=" Choose Location"   onPlaceSelected={(place) => { setFormData({...formData,location:place.formatted_address})}} />
                    <small className="form-text">Choose your location</small>
               </div>
               <div className="form-group">
                    <input type="text" placeholder="* Skills" name="skills" value={skills} onChange={(e) => onChange(e)} />
                    <small className="form-text">Please use comma separated values (eg.HTML,CSS,JavaScript,PHP)</small>
               </div>
               <div className="form-group">
                    <input type="text" placeholder="Github Username" name="githubusername" value={githubusername} onChange={(e) => onChange(e)}/>
                    <small className="form-text">If you want share your latest repos, include your username</small>
               </div>
               <div className="form-group">
                    <textarea placeholder="A short bio of yourself" name="bio" value={bio} onChange={(e) => onChange(e)}></textarea>
                    <small className="form-text">Tell us a little about yourself</small>
               </div>
               <div className="my-2">
                    <button onClick={() => toggleBetweenSocial(!displaySocialLinks)}type="button" className="btn btn-light">Add Social Network Links</button>
               </div>
                {displaySocialLinks && <Fragment>
                <div className="form-group social-input">
                    <i className="fab fa-twitter fa-2x"></i>
                    <input type="text" placeholder="Twitter URL" name="twitter" value={twitter} onChange={(e) => onChange(e)} />
                </div>

                <div className="form-group social-input">
                    <i className="fab fa-facebook fa-2x"></i>
                    <input type="text" placeholder="Facebook URL" name="facebook" value={facebook} onChange={(e) => onChange(e)} />
                </div>

                <div className="form-group social-input">
                    <i className="fab fa-youtube fa-2x"></i>
                    <input type="text" placeholder="YouTube URL" name="youtube" value={youtube} onChange={(e) => onChange(e)} />
                </div>

                <div className="form-group social-input">
                    <i className="fab fa-linkedin fa-2x"></i>
                    <input type="text" placeholder="Linkedin URL" name="linkedin" value={linkedin} onChange={(e) => onChange(e)}/>
                </div>

                <div className="form-group social-input">
                    <i className="fab fa-instagram fa-2x"></i>
                    <input type="text" placeholder="Instagram URL" name="instagram" value={instagram} onChange={(e) => onChange(e)}/>
                </div>
                        </Fragment>}
            <button type="submit" className="btn btn-primary my-1" onClick={(e) =>onSubmit(e)}>Submit</button>
            <Link to='/dashboard' className="btn btn-light my-1" >Go Back</Link>
        </form>
    </section>
    )
}

CreateProfile.propTypes = {
    createProfile: PropTypes.func.isRequired
}


export default connect(null,{createProfile})(withRouter(CreateProfile))
